import { select } from "d3-selection";
import { tree, hierarchy } from "d3-hierarchy";
import { linkVertical } from "d3-shape";
import {
  annotation,
  annotationCalloutCircle,
  annotationCalloutElbow,
  annotationLabel,
} from "d3-svg-annotation";

// selectors
const treeSelection = "#intro-tree-chart";

const dims = select(treeSelection).node().getBoundingClientRect();
console.log(dims);
const radius = 12;
// func to generate tree data
function generateTree(idx, num) {
  if (num === 1) {
    return { id: idx, children: [] };
  } else {
    let split = 1 + Math.floor((num - 1) * 0.52);
    return {
      id: idx + split,
      children: [
        generateTree(idx, split),
        generateTree(idx + split + 1, num - split),
      ],
    };
  }
}

// generate a random tree
const treeDataset = generateTree(0, 3);

// draw tree
let height = 400;
let width = dims.width * 0.9;
const margin = {
  top: 30,
  bottom: 0,
  left: width * 0.14,
  right: 0,
};
// define svg
const svg = select(treeSelection)
  .append("svg")
  .attr("id", "annotated-tree")
  .attr("width", width)
  .attr("height", height)
  .attr("viewBox", `${0} ${0} ${width} ${height}`);

// append g for framing
const g = svg
  .append("g")
  .attr("transform", `translate(${margin.left}, ${margin.top})`);

// init tree
const treePlot = tree().size([width * 0.58, height - 100]);

// make data hiearchical
const root = hierarchy(treeDataset, (d) => d.children);

// apply tree to hierachical data
const treeData = treePlot(root);

const path = linkVertical()
  .x((d) => d.x)
  .y((d) => d.y);

// draw links
const treePaths = g
  .append("g")
  .attr("class", "tree-link-g")
  .selectAll("path")
  .data(treeData.links())
  .join("path")
  .attr("d", path)
  .attr("class", "link")
  .attr("fill", "none")
  .attr("stroke", "#555")
  .attr("stroke-opacity", 0.58)
  .attr("stroke-width", radius / 2);

const nodeData = treeData.descendants();
console.log(nodeData);
// draw nodes
const treeNodes = g
  .append("g")
  .attr("class", "tree-node-g")
  .selectAll("circle")
  .data(nodeData);

const treeCircles = treeNodes
  .join("circle")
  .attr("fill", "black")
  .attr("r", radius)
  .attr("cx", (d) => d.x)
  .attr("cy", (d) => d.y)
  .attr("stroke", "#555")
  .attr("stroke-width", 1);

// select("svg")
//   .append("text")
//   .attr("class", "chartTitle")
//   .attr("x", 480)
//   .attr("y", 40)
//   .attr("text-anchor", "middle")
//   .html("Playing with d3-annotation");

const annotations = [
  {
    type: annotationLabel,
    note: {
      label: "Is the top-most node in a tree",
      //   bgPadding: 20,
      title: "Root Node",
      wrap: 100,
    },
    connector: {
      end: "dot", // 'dot' also available
      endScale: 0.5,
    },
    x: nodeData[0]["x"] + margin.left,
    y: nodeData[0]["y"] + margin.top, // margin top
    dy: 0,
    dx: -width / 5,
  },
  {
    type: annotationLabel,
    note: {
      label: "Represent choices to be made",
      //   bgPadding: 20,
      title: "Decision Nodes",
      //   wrap: 90,
    },
    connector: {
      end: "dot", // 'dot' also available
      endScale: 0.5,
    },
    x: nodeData[1]["x"] + margin.left,
    y: nodeData[1]["y"] + margin.top, // margin top
    dy: 0,
    dx: -width / 4.4,
  },
  {
    type: annotationLabel,
    note: {
      label: "Indicate a final outcome",
      //   bgPadding: 20,
      title: "Leaf Nodes",
      //   wrap: 90,
    },
    connector: {
      end: "dot", // 'dot' also available
      endScale: 0.5,
    },
    x: nodeData[4]["x"] + margin.left,
    y: nodeData[4]["y"] + margin.top, // margin top
    dy: -1,
    dx: width / 5,
  },
].map(function (d) {
  d.color = "grey";
  return d;
});

const makeAnnotations = annotation()
  //   .editMode(true)
  .type(annotationLabel)
  .annotations(annotations);

select("#annotated-tree")
  .append("g")
  .attr("class", "annotation-group")
  .call(makeAnnotations);
